import Vue from 'vue';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			activity_id: 0,
			scroll_height: 700,
			teamName: '',
			userNumber: '',
			showInput: false,
			inputTimeStamp: 0,
			showSearchUser: false,
			showSearchError: false,
			showToast: false,
			searchUser: {},
			otherUser: [],
			toastContent: ''
		}
	},
	computed: {
		...mapState({
			user: state => state.AuthUser,
		}),
	},
	onLoad(option) {
		this.activity_id = option.activity_id;
	},
	methods: {
		submit() {
			console.log('123')
			if (this.teamName == '') {
				this.toastContent = '队伍名称不能为空'
				this.showToast = true;
				return;
			}
			let teamUser = [];
			teamUser.push(this.user.user_id);
			if (this.otherUser.length != 0) {
				for (let item in this.otherUser) {
					teamUser.push(this.otherUser[item].user_id);
				}
			}
			Vue.prototype.$http.request({
				url: '/activities/team/create',
				method: 'POST',
				params: {
					activity_id: this.activity_id,
					team_name: this.teamName,
					team_user: teamUser,
				}
			}).then(res => {
				this.$refs.notification.open({
					type: 'success',
					content: '报名成功',
					timeout: 1500,
					isClick: false
				});
				setTimeout(() => {
					uni.navigateBack({
						delta: 1
					});
				}, 1500)
			})
		},
		pushNewUser() {
			if (this.user.user_id == this.searchUser.user_id) {
				this.$refs.notification.open({
					type: 'err',
					content: '该成员已加入队伍',
					timeout: 1500,
					isClick: false
				});
				return;
			}
			for (let index in this.otherUser) {
				if (this.otherUser[index].user_id == this.searchUser.user_id) {
					this.$refs.notification.open({
						type: 'err',
						content: '该成员已加入队伍',
						timeout: 1500,
						isClick: false
					});
					return;
				}
			}
			this.otherUser.push(this.searchUser);
			this.showInput = false;
			this.showSearchUser = false;
			this.userNumber = '';
			setTimeout(() => {
				this.searchUser = {};
			}, 1000)
		},
		addNewUser() {
			this.showInput = true;
		},
		hideModal() {
			this.showInput = false;
		},
		hideToast() {
			this.showToast = false;
		},
		searchTips(event) {
			this.showSearchError = false;
			this.showSearchUser = false;
			this.inputTimeStamp = event.timeStamp;
			setTimeout(() => {
				//1s后比较二者是否还相同（因为只要还有事件触发，inputTimeStamp就会被改写，不再是当前事件函数的时间戳）
				if (this.inputTimeStamp == event.timeStamp) {
					this.getUser();
				}
			}, 600);
		},
		getUser() {
			Vue.prototype.$http.request({
				url: '/users/search/number',
				method: 'POST',
				params: {
					user_number: this.userNumber
				},
			}).then(res => {
				this.searchUser = res.data.data;
				this.showSearchUser = true;
			}).catch(error => {
				this.showSearchError = true;
				console.log(error);
			})
		}
	}
}
